body {
    background-color: #43c5d3;
    min-height: 100vh;
}

.logo-wrapper {
    margin-top: 25px;
    height: 150px;
    width: auto;
    display: flex;
    justify-content: center;
}

.logo {
    height: inherit;
}

.search-wrapper {
    width: 100%;
    height: auto;
    display: grid;
    justify-items: center;
    grid-template-columns: 1fr 4fr 1fr;
    grid-template-rows: 1fr 1fr;
}

.search-title {
    grid-row: 1;
    grid-column: 2;
    font-weight: 400;
}

.search-bar {
    grid-column: 2;
    grid-row: 2;
    display: grid;
    width: 100%;
    height: 100%;

    > input {
        border: none;
        box-shadow: 0px 2px 7px rgb(45, 42, 42);
        border-radius: 5px;
        padding: 15px;
    }
}

.search-button {
    grid-row: 3;
    width: 25%;
    justify-self: end;
    border: none;
    border-radius: 8px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    background-color: rgb(73, 59, 76);
    color: white;
}

.post-area {
    height: auto;
    width: 100%;
    display: grid;
    justify-items: center;
    grid-template-columns: 1fr 3.5fr 1fr;
    grid-template-rows: auto;
}

.message-container {
    border-radius: 10px;
    background: white;
    box-shadow: 3px 8px 0px rgb(55, 96, 98);
    width: 70%;
    margin: 25px;
    grid-column: 1 / 4;
}

.button-container {
  margin: 5px;
  display: grid;
  border-radius: 7px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  grid-auto-flow: column;
  grid-row: 2;
  grid-column: 3;
  gap: 5px;
}

.my-button {
    border: 0px solid rgb(81, 135, 180);
    background-color: #43c5d3;
    color: white;
    border-radius: 3px;
    min-width: 10vw;
    text-transform: capitalize;
    margin: 5px;
    min-height: 30px;
    box-shadow: 0px 5px 0px rgb(55, 96, 98);
}

.like-button {
    background-color: rgb(234, 111, 189);
    box-shadow: 0px 5px 0px rgb(158, 64, 119);
    font-weight: bold;
}

.delete-button {
    background: rgb(214, 90, 90);
    box-shadow: 0px 5px 0px rgb(144, 33, 26);
}

.save-button {
    background-color: rgb(118, 226, 125);
    box-shadow: 0px 5px 0px rgb(49, 138, 21);
    padding: 0;
}

.message-area {
    padding: 10px;
    color: rgb(82, 81, 81);
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
}

.user-wrapper {
    color: rgb(78, 81, 81);
    grid-column: 1;
    grid-row: 1;
    font-weight: bold;
}

.content-wrapper {
    width: 100%;
    color: rgb(65, 54, 54);
    font-weight: 400;
    grid-column: 1 / 4;
    grid-row: 2;
    min-height: 50px;
    justify-self: center;
    border-radius: 0px;
    background-color: #e3e7e7;
    padding: 5px;
    box-shadow: 0px 4px 0px rgb(138, 171, 173);
    opacity: 1;
}

.icon-wrapper {
    width: auto;
    padding: 5px;
    height: 70px;
    display: grid;
    grid-row: 1;
    grid-column: 3;
    grid-template-columns: 1fr 1fr 1fr;
}

.icon {
    width: inherit;
    height: inherit;
    object-fit: contain;
    grid-column: 3;
    align-self: center;
}

.edit-wrapper {
    border: none;
    border-radius: 5px;
    background-color: white;
    font-size: inherit;
    color: rgb(82, 81, 81);
    font-family: inherit;
    width: 100%;
    height: 100%;
    resize: none;
    display: flex;
    padding: 0;
    margin: 0;
}

.edit-button-wrapper {
    grid-column: 1;
}

.date-wrapper {
    font-size: 0.8em;
    font-style: italic;
    grid-row: 1;
    grid-column: 1 / 4;
    margin-top: 25px;
}